// Product-cart
.product-card {
    padding-top: 55px;
    padding-bottom: 90px;

    &__inner {
        padding-top: 25px;
        padding-bottom: 60px;
    }

    &__row {
        @include row-flex();
        justify-content: space-between;

        .item-6 {
            @include col();
            @include size(6);
            @include size-lg(12);
        }
    }


    &__title {
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 24px;
        line-height: 31px;

        letter-spacing: 4px;
        text-transform: uppercase;

        color: #393939;
    }

    &__card-star {
        margin-left: 0;
        margin-bottom: 30px;

        & svg {
            margin-right: 7px;
        }
    }

    &__cat,
    &__mark {
        line-height: 23px;

        letter-spacing: 1px;
        text-transform: uppercase;

        color: #393939;

        span {
            padding-left: 10px;
            font-size: 18px;
            color: #c9c9c9;
            text-transform: none;
        }
    }

    &__cat {
        margin-bottom: 10px;
    }

    &__mark {
        margin-bottom: 30px;
    }

    &__price {
        margin-bottom: 30px;

        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        text-transform: uppercase;

        color: #446657;
    }

    &__order {
        margin-bottom: 30px;
        line-height: 19px;

        letter-spacing: 1px;

        color: #393939;
    }

    &__counter {
        margin-bottom: 30px;
    }

    &__colors {
        margin-bottom: 10px;

        .colors-container {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .colors__title {
            margin-bottom: 15px;
            font-weight: 400;
            font-size: 18px;
            line-height: 21px;

            color: #5d5d5d;
        }

        .colors__inner {
            position: relative;
            display: inline-block;

            margin-bottom: 20px;

            img {
                width: 100px;
                height: 50px;

                cursor: pointer;
            }

            span {
                position: absolute;
                top: 30px;
                left: 7px;
                font-family: $Montserrat-font;
                font-size: 14px;
                line-height: 17px;

                color: #ffffff;
            }
        }

        @media all and (max-width: 991px) {
            .colors-container {
                justify-content: start;
            }

            .colors__inner {
                margin-right: 20px;
            }
        }
    }

    &__button {
        width: 100%;
    }

    &__tabs {
        margin-bottom: 80px;
    }
}

.product-slider-for {
    width: 564px;
    margin-bottom: 20px;}

.slider-nav {
    width: 564px;

    & img {
        max-width: 173px;
        max-height: 173px;
    }
}

.similar-products {
    .card {
        width: 100%;
    }
    &__title {
        margin-bottom: 24px;
        font-size: 15px;
        line-height: 22px;

        letter-spacing: 3px;
        text-transform: uppercase;

        color: #0a0a0a;
    }

    &__row {
        @include row-flex();

        .item-3 {
            @include col();
            @include size(3);
            @include size-lg(4);
            @include size-md(6);
            @include size-sm(12);
        }
    }
}