// Contacts
.contacts {
    padding-top: 55px;
    padding-bottom: 90px;

    &-list-inner {
        margin-top: 15px;
        padding: $base-line-height * 2 0;
        background-color: #f9f9f9;
    }

    &-form-inner {
        padding: 37px 0 90px 0;
        background-color: #f9f9f9;
    }

    &-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        &__item {
            position: relative;
            margin: 0 30px;
            padding-left: 35px;
            font-size: 14px;
            line-height: 22px;
            font-family: $Montserrat-font;

            color: #000000;

            &:first-child {
                margin: 0;
            }

            &:last-child {
                margin: 0;
            }
        }

        &__icon {
            position: absolute;
            left: 0;
        }

        &__title {
            margin-bottom: 16px;
            font-size: 15px;
            line-height: 23px;
            font-weight: 700;

            letter-spacing: 1px;
            text-transform: uppercase;

            color: #393939;
        }

        &__link {
            color: inherit;
            transition: $base-transition;

            &:hover {
                color: #446657;
            }
        }
    }

    &__form {
        width: 560px;
        padding: 60px;
        color: #393939;
        background-color: red;
        border: 1px solid #d7d7d7;
    }
}
