// Tabs
.tabs {
    &__title {
        margin-bottom: 10px;
        background-color: #f9f9f9;
    }
    &__title-list {
        display: flex;
        @include sm (justify-content, space-between);
    }
    &__title-item {
        display: inline-block;
        padding: 15px 45px;
        @include sm (font-size, 12px);
        @include sm (padding-left, 5px);
        @include sm (padding-right, 5px);

        line-height: 21px;
        letter-spacing: 1.2px;
        border-bottom: 1px solid transparent;

        transition: $base-transition;

        &:hover {
            border-bottom: 1px solid #d7d7d7;
            cursor: pointer;
        }

        &--active {
            font-weight: 700;
            color: #446657;
            border-bottom: 1px solid #446657;

            &:hover {
                border-bottom: 1px solid #446657;
                cursor: default;
            }
        }
    }

    &__row {
        @include row-flex();
    }

    &__col-inner {
        @include col();
        @include size(12);
        width: 55%;
        @include lg(width, 70%);
        @include md(width, 100%);
        padding: 15px 0;
        
        strong {
            display: inline-block;
            width: 35%;
            padding-left: 10px;
            font-weight: 400;
            line-height: 23px;
            color: #342834;
            @include sm(width, 50%)

        }
        
        span {
            display: inline-block;
            color: #393939;
            @include md(width, 50%)
        }

        border-bottom: 1px solid #d7d7d7;

        &:last-child {
            border-bottom: none;
        }
    }
}
