.modal-window-overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5000;
    width: 100%;
    height: 100vh;

    display: none; //
    align-items: center;
    justify-content: center;

    background-color: rgba($base-menu-color, 0.66);
}

.modal-window {
    max-width: 470px;
    max-height: 470px;
    width: 100%;
    height: 100%;
    position: relative;
    padding: 60px;
    color: #ffffff;
    background-color: #5b7d6e;

    &__close {
        position: absolute;
        display: block;
        width: 14px;
        height: 14px;
        top: 32px;
        right: 24px;
        cursor: pointer;

        &::before,
        &::after {
            position: absolute;
            content: "";
            display: block;
            width: 14px;
            height: 1px;
            background-color: #ffffff;
        }

        &::before {
            transform: rotate(45deg);
        }

        &::after {
            transform: rotate(-45deg);
        }
    }

    &__title {
        margin-bottom: 75px;
        font-family: $lora-font;
        font-size: 28px;
        line-height: 30px;
        letter-spacing: 3px;
        font-weight: 400;
    }
}

.form {
    &__row {
        position: relative;
        margin-bottom: 50px;
    }

    label {
        position: absolute;
        top: -26px;
        display: block;
        font-size: 12px;
        line-height: 30px;
        letter-spacing: 1.5px;
        color: rgba(#ffffff, 0.75);

        opacity: 0;
    }

    input {
        width: 100%;
        height: 36px;
        border: none;
        border-bottom: 1px solid #ffffff;
        background-color: transparent;
        font-size: 18px;
        line-height: 30px;
        letter-spacing: 3px;
        color: #ffffff;
        outline: none;

        &::placeholder {
            color: #ffffff;
        }

        &:focus label {
            opacity: 1;
        }
    }

    &__button {
        display: block;
        width: 100%;
        height: 72px;
        margin: 0 auto;
        letter-spacing: 3px;
        font-size: 18px;
        color: #5b7d6e;
        background-color: #ffffff;
        border: 1px solid #ffffff;

        &:hover {
            color: #ffffff;
            background-color: #5b7d6e;
        }
    }
}
