// Page-news
.page-news {
    padding-top: 55px;
    padding-bottom: 120px;

    &__button {
        padding: 0 50px;
        position: relative;
        float: right;
        line-height: 46px;
        color: $base-green-color;
        border: 1px solid $base-green-color;
        background-color: $base-white-color;

        &::before {
            position: absolute;
            top: 18px;
            left: 15px;
            content: "";
            display: block;
            width: 19px;
            height: 12px;
            background: url(../img/svg/arrow-left.svg);
            background-repeat: no-repeat;
        }

        &:hover {
            color: $base-white-color;
            border: 1px solid $base-green-color;
            background-color: $base-green-color;
        }

        &:hover::before {
            background: url(../img/svg/arrow-left-white.svg)
        }
    }

    & aside {
        padding-left: 40px;
        border-left: 6px solid #f9f9f9;
    }

    &__aside-title {
        margin-top: -15px;
        margin-bottom: 25px;
        font-size: 15px;
        font-weight: 700;
        letter-spacing: 1px;
        text-transform: uppercase;
    }

    &__aside-desc {
        margin-bottom: $base-line-height;
    }

    &__news-date {
        display: inline-block;
        margin-bottom: 5px;
        font-size: 12px;
        font-weight: 700;
        color: #a6a6a6;
    }

    &__news-desc {
        font-size: 14px;
        line-height: 23px;
        color: #393939;

    }

    & .text-inner {
        display: inline-block;
    }

    & .news-item {
        padding-right: 40px;
        margin-top: 25px;
        margin-bottom: $base-line-height;
        padding-bottom: $base-line-height + 5;
        width: 100%;
        border: none;
        border-bottom: 1px solid #d7d7d7;

        &__img {
            margin-bottom: 50px;
        }

        &__title {
            margin-bottom: 35px;
            padding-left: 0;
            width: 100%;
            font-size: 25px;
            line-height: 30px;
            letter-spacing: 3px;
        }

        &__desc {
            padding-left: 0;
            padding-bottom: $base-line-height - 5;
            font-family: $arimo-font-base;
            font-size: $base-font-size + 2;
            line-height: 28px;
        }

        &__date {
            font-size: $base-font-size;
            line-height: 14px;
            color: #c9c9c9;
        }

        &__author {
            margin-top: 7px;
            font-size: $base-font-size + 2;
            line-height: 28px;
        }
    }

    @media all and (max-width: 767px) {
        & .row-fl__col-9 {
            flex-basis: 100%;
        }

        & .news-item {
            padding-right: 0;
        }

        & aside {
            display: none;
        }
    }
}
