.counter {
    &__inner {
        // height: 42px;
        display: inline-block;
        border-top: 1px solid #b8b8b8;
        border-bottom: 1px solid #b8b8b8;

        input {
            width: 42px;

            font-size: 18px;
            font-weight: 700;
            text-align: center;
            color: #393939;
            
            border: none;
        }

        button {
            display: inline-block;
            width: 48px;
            height: 48px;
            padding: 0;
            // line-height: 40px;
            font-size: 26px;
            color: #342834;
            border: none;
            border-left: 1px solid #b8b8b8;
            border-right: 1px solid #b8b8b8;
            background-color: transparent;
            cursor: pointer;
        }

        button:focus,
        input:focus {
            outline: 0;
        }
    }
}
