// Pagination
.pagination {
    display: flex;
    justify-content: center;

    &__item {
        margin: 0 6px;

        &:first-child {
            margin-right: 12px;
        }

        &:last-child {
            margin-left: 12px;
            &:hover svg>path {
                stroke: #393939;
            }
        }
    }

    &__link {
        color: #a6a6a6;
        transition: $base-transition;

        &:hover {
            color: #393939;
        }

        &--active {
            color: #393939;
        }
    }
}
