// All-news
.all-news {
    padding-top: 55px;
    padding-bottom: 120px;

    &__button {
        line-height: 46px;
        color: $base-green-color;
        border: 1px solid $base-green-color;
        background-color: $base-white-color;

        &:hover {
            color: $base-white-color;
            border: 1px solid $base-green-color;
            background-color: $base-green-color;
        }
    }

    @media all and (max-width: 575px) {
        & {
            padding-bottom: 60px;
        }
    }

    & .news-item {
        border: none;
        margin-bottom: 60px;

        &:hover,
        &:focus,
        &:active {
            border: none;
        }

        &__img {
            margin-bottom: 25px;
        }

        &__date {
            font-size: $base-font-size - 2;
            line-height: 14px;
            color: #c9c9c9;
        }

        &__title {
            margin-top: $base-line-height - 5;
            padding-left: 0;
            width: 100%;
        }

        &__desc {
            padding-left: 0;
            font-family: $arimo-font-base;
            font-size: $base-font-size - 1;
            line-height: 23px;
        }
    }

    @media all and (max-width: 991px) {
        .row-fl {
            justify-content: space-around;
        }
    }

    @media all and (max-width: 575px) {
        & .news-item {
            width: 290px;
        }
    }
}
